body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: rgb(153, 153, 153);
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  height: auto;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
  width: 100%;
}
